export function getPixelRatio() {
  return window ? window.devicePixelRatio : 1;
}

export const isBrowser = typeof window !== 'undefined' && typeof window.document !== 'undefined';

export function isString(data: any) {
  return typeof data === 'string';
}

export function isNil(value: any) {
  return value === null || value === undefined;
}

export function toString(value: any) {
  if (isNil(value)) {
    return '';
  }
  return value.toString();
}

// 
export function upperFirst(value: string) {
  const str = toString(value);
  return str.charAt(0).toUpperCase() + str.substring(1);
}

/**
 *  throttleArray
 * 
 * @param arr 
 * @param cb 
 * @param count render
 * @param interval render
 */
export function throttleArray(arr: any[], cb: Function, count: number, interval: number): void {
  let timer: any;
  let index = 1;
  const length = arr.length;
  function start() {
    for (let idx = 0; idx < Math.min(count || 1, length - index); idx++) {
      const item = arr[index - 1];
      index++;
      cb(item);
    }
  }
  function loop() {
    timer = setInterval(() => {
      if (length - index === 0) {
        clearInterval(timer);
      }
      start();
    }, interval || 200);
  }
  loop();
}
/**
 *  throttleArray
 * 
 * @param arr 
 * @param cb 
 * @param count render
 * @param interval render
 * @return ,
 */
export function throttleArrayFunc(arr: any[], cb: Function, count: number, interval: number) {
  let timer: any;

  function start() {
    for (let idx = 0; idx < Math.min(count || 1, arr.length); idx++) {
      const item = arr.shift();
      cb(item);
    }
  }
  function loop() {
    timer = setInterval(() => {
      if (arr.length === 0) {
        clearInterval(timer);
      }
      start();
    }, interval || 200);
  }
  return loop;
}
