
import { isBrowser, isString } from '../../util/util';
import { GCanvas } from '../../interface';
import { Base } from './base';

const PX_SUFFIX = 'px';

// 
export abstract class AbstractCanvas extends Base implements GCanvas {

  constructor(cfg: any) {
    super(cfg);
    this.initContainer();
    this.initDom();
    this.initEvents();
    this.initShape();
  }

  getDefaultCfg() {
    const cfg = super.getDefaultCfg();
    cfg['cursor'] = 'default';
    return cfg;
  }
  /**
   * @protected
   * 
   */
  initContainer() {
    let container = this.get('container');
    if (isString(container)) {
      container = document.getElementById(container);
      this.set('container', container);
    }
  }


  /**
   * @protected
   *  DOM
   */
  initDom() {
    const element = this.createDom();
    this.set('el', element);
    // 
    const container = this.get('container');
    container.appendChild(element);
    // 
    this.setDOMSize(this.get('width'), this.get('height'));
  }
  /**
   * @protected
   * 
   */
  initEvents() {}

  /**
   * @protected
   * 
   */
  initShape() {}

  /**
   *  DOM 
   * @param {number} width  
   * @param {number} height 
   */
  setDOMSize(width: number, height: number) {
    const el = this.get('el');
    if (isBrowser) {
      el.style.width = width + PX_SUFFIX;
      el.style.height = height + PX_SUFFIX;
    }
  }

  /**
   * 
   * @return {HTMLElement} 
   */
  abstract createDom(): HTMLElement | SVGSVGElement;

  /**
   * 
   */
  get engine() {
    return 'canvas';
  }

  /**
   * 
   */
  clear() {}

  destroy() {
    if (this.get('destroyed')) {
      return;
    }
    this.clear();
    super.destroy();
  }
}
